<template>
  <div class="me">
    <div class="user-poster">
      <van-image class="avatar" :src="user.avatar" fit="cover" round />
      <div class="text">{{$store.user.nickname}}</div>
    </div>
    <van-grid :border="false">
      <van-grid-item icon="pending-payment" text="待付款" @click="$Toast('有待开发')" />
      <van-grid-item icon="records" text="待发货" @click="$Toast('有待开发')" />
      <van-grid-item icon="tosend" text="已发货" @click="$Toast('有待开发')" />
      <van-grid-item icon="logistics" text="已完成" @click="$Toast('有待开发')" />
    </van-grid>

    <van-cell-group class="user-group">
      <van-cell icon="records" title="全部订单" is-link :to="{name:'orderList'}" />
    </van-cell-group>

    <van-cell-group>
      <van-cell icon="like-o" title="我的收藏" is-link :to="{name:'likeList'}" />
      <van-cell icon="description" title="我的收货地址" is-link :to="{name:'addressList', query:{from:'me'}}" />
      <van-cell icon="setting-o" title="用户设置" is-link :to="{name:'userSetting'}" />
    </van-cell-group>
  </div>
</template>

<script>
  export default {
    data() {
      return {

      }
    },
    computed: {
      user() {
        return this.$store.user
      }
    },
    methods: {

    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .me {
    padding-bottom: 50px;
  }

  .user-poster {
    background: #f44;
    padding: 5vw;
    display: flex;
    color: peachpuff;
  }

  .user-poster .text {
    line-height: 20vw;
    display: inline-block;
    margin-left: 2vw;
  }

  .avatar {
    /* margin: 40vw; */
    width: 20vw;
    height: 20vw;
  }

  /* .avatar img{
    width: 10vw;
  } */

  .user-group {
    margin-bottom: 15px;
  }

  .user-links {
    padding: 15px 0;
    font-size: 12px;
    text-align: center;
    background-color: #fff;
  }

  .van-icon {
    display: block;
    font-size: 24px;
  }
</style>


